	box-sizing: border-box;
}

.skip-link-desk {
	position: absolute;
	top: -40px;
	left: 0;
	background-color: #bf1722;
	color: white;
	padding: 8px;
	z-index: 100;
	display: none;
}

.skip-link-desk:focus {
	top: 0;
}

.skip-link-mob {
	position: absolute;
	top: -40px;
	left: 0;
	background-color: #bf1722;
	color: white;
	padding: 8px;
	z-index: 100;
}

.skip-link-mob:focus {
	top: 0;
}

body {
	padding: 0;
	margin: 0;
	font-family: sans-serif;
	overflow-x: hidden;
}
img {
	display: block;
}

.app-bar {
	padding: 8px 16px;
	background-color: rgb(45, 45, 45);
	display: grid;
	grid-template-columns: auto 1fr auto;
	gap: 10px;
	position: sticky;
	top: 0;
	z-index: 99;
	box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.app-bar .app-bar__menu {
	display: flex;
	align-items: center;
}

.app-bar .app-bar__menu button {
	background-color: transparent;
	border: none;
	font-size: 30px;
	padding: 8px 10px;
	cursor: pointer;
	color: white;
}

.app-bar .app-bar__brand {
	display: flex;
	align-items: center;
}

.app-bar .app-bar__brand h1 {
	color: #ffffff;
	text-transform: uppercase;
	font-size: 22px;
	user-select: none;
}

.app-bar .app-bar__navigation {
	position: absolute;
	top: 50px;
	left: -180px;
	width: 150px;
	transition: all 0.3s;
	padding: 8px;
	background-color: rgb(45, 45, 45);
	overflow: hidden;
}

.app-bar .app-bar__navigation.open {
	left: 0;
}

.app-bar .app-bar__navigation ul {
	list-style: none;
}
.app-bar .app-bar__navigation ul li a {
	display: inline-block;
	text-decoration: none;
	list-style: none;
	color: white;
	padding: 14px 8px;
	margin-bottom: 5px;
	width: 100%;
	font-weight: bold;
}

.img-hero {
	width: 100%;
	height: 70vh;
	object-fit: cover;
	display: block;
	margin: 0 auto;
}

.container {
	margin: 15px 40px;
}

.list-restaurant {
	margin-top: 40px;
	text-align: center;
}

#restaurant-list {
	display: flex;
	flex-wrap: wrap;
	gap: 20px;
	justify-content: center;
}

.restaurant-card {
	border: 1px solid #ccc;
	border-radius: 8px;
	width: 160px;
	padding: 16px;
	text-align: center;
}

.restaurant-card img {
	width: 100%;
	height: auto;
	border-radius: 8px;
}

.restaurant-name {
	font-size: 1.2em;
	margin: 8px 0;
}
.restaurant-link {
	display: inline-block;
	margin-top: 12px;
	padding: 16px 12px;
	background-color: rgb(66, 66, 66);
	color: white;
	text-decoration: none;
	border-radius: 4px;
	transition: background-color 0.2s;
}

.restaurant-link:hover {
	background-color: rgb(0, 0, 0);
}

.restaurant-detail {
	max-width: 800px;
	margin: 0 auto;
	padding: 20px;
	font-family: Arial, sans-serif;
}

.restaurant-detail .restaurant__title {
	font-size: 2em;
	margin-bottom: 10px;
	color: #333;
	text-align: center;
}

.restaurant-detail .restaurant__image {
	width: 100%;
	height: auto;
	border-radius: 8px;
	margin-bottom: 20px;
}

.restaurant-detail .restaurant__info,
.restaurant-detail .restaurant__description,
.restaurant-detail .restaurant__menu,
.restaurant-detail .restaurant__reviews {
	margin-bottom: 20px;
	padding: 15px;
	background: #f9f9f9;
	border-radius: 8px;
	font-weight: 600;
}

.restaurant-detail .restaurant__info h3,
.restaurant-detail .restaurant__description h3,
.restaurant-detail .restaurant__menu h3,
.restaurant-detail .restaurant__reviews h3 {
	margin-top: 0;
	color: #555;
	font-size: 1.5rem;
}

.restaurant-detail .restaurant__info h4,
.restaurant-detail .restaurant__menu h4 {
	font-size: 1.1em;
	color: #666;
	margin-bottom: 5px;
}

.restaurant-detail .restaurant__menu ul {
	list-style-type: disc;
	padding-left: 20px;
}

.restaurant-detail .review {
	margin-bottom: 15px;
	padding: 10px;
	border-bottom: 1px solid #ddd;
}

.restaurant-detail .review p {
	font-family: sans-serif;
	margin: 5px 0;
}

.restaurant-detail .review strong {
	font-size: 1.2rem;
	color: #444;
}

.restaurant-detail p {
	color: #333;
	line-height: 1.6;
}

.like {
	width: 55px;
	height: 55px;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #db0000;

	position: fixed;
	bottom: 16px;
	right: 16px;
	border-radius: 50%;
	border: 0;

	font-size: 18px;
	color: white;
	cursor: pointer;
}

.footer {
	text-align: center;
	padding: 10px 0;
	font-size: 14px;
	color: rgb(255, 255, 255);
	background-color: rgb(45, 45, 45);
	font-weight: bold;
}
